.App {
  text-align: center;
}

.App-header {
  background: linear-gradient(#eaeaec, #b7b6b6);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
  min-width: 100%;
  
}

.App-link {
  color: #61dafb;
}
#header-h1 {
  font-size: 5vw;
  color: #333333;
}

.header-p {
  color: #4b4b4b;
  font-weight: 549;
}

#header-h1, .header-p {
  padding: 15px;
}

.Nav {
  position: absolute;
  top: 0px;
  left: 0px;
}

@media screen and (max-width: 769px) {
  #header-h1 {
    font-size: 6vh;
  }
  .header-p {
    font-size: 2.2vh
  }
}